.project-heading {
  text-align: center;
  margin-top: 30px;
  font-size: 2.5rem;
  color: #343a40;
}

.project-subheading {
  text-align: center;
  margin-bottom: 30px;
  color: #6c757d;
}

.project-card-view {
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  overflow: hidden; /* Ensures that images and other content stay within the card boundaries */
}

.project-image {
  width: 100%;
  height: auto;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.project-image-full {
  width: 100%; /* Ensure the image takes up the full width of the card */
  height: auto; /* Maintain aspect ratio */
  max-width: 100%; /* Prevent image overflow */
  object-fit: cover; /* Ensures the image fits within its container while maintaining aspect ratio */
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.horizontal-scroll-container {
  display: flex;
  overflow-x: auto;
  padding: 10px;
}

.horizontal-scroll-container img {
  margin-right: 10px;
  height: auto; /* Maintain aspect ratio */
  width: 100%; /* Ensure images in the scroll container take full width */
  border-radius: 8px;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  .project-card-view {
    margin: 0 auto; /* Center the cards on mobile */
  }

  .horizontal-scroll-container {
    flex-direction: column;
    align-items: center; /* Center images on mobile */
  }

  .horizontal-scroll-container img {
    width: 100%; /* Make sure images take full width on small screens */
    margin-right: 0;
    margin-bottom: 10px; /* Add space between images on mobile */
  }

  .project-image-full {
    width: 100%; /* Ensure the full-width image resizes for small screens */
    height: auto; /* Maintain aspect ratio */
  }
}

/* Ensures the footer stays at the bottom */
html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.project-section {
  flex-grow: 1;
}

.mt-auto {
  margin-top: auto;
}
